body {
  margin: 0;
  padding: 0;
  text-align: center;
  vertical-align: middle;
  background-image: url(/BuckeyeCharge/static/media/catch.8b4feffd.JPG);
  background-attachment: fixed !important;
  background-size: contain !important;
  position: relative;
  display: table;
  width: 100%;
  background-repeat: no-repeat !important;
  background-position: 50.00% 20.51% !important;
  background-size: 100% !important;
  background-color: transparent !important;
}

.slider {
  position: relative;
  width: 100%;
  margin: 0 auto;
  height: 450px;
  overflow: hidden;
  white-space: nowrap;
  z-index: 50;
}

.slider-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.backArrow, .nextArrow {
  height: 50px;
  width: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #f9f9f9;
  border-radius: 50%;
  cursor: pointer;
  transition: -webkit-transform ease-in .1s;
  transition: transform ease-in .1s;
  transition: transform ease-in .1s, -webkit-transform ease-in .1s;
}

.nextArrow:hover, .backArrow:hover {
  transition: -webkit-transform ease-in .1s;
  transition: transform ease-in .1s;
  transition: transform ease-in .1s, -webkit-transform ease-in .1s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}

.nextArrow {
  position: absolute;
  top: 50%;
  right: 25px;
  z-index: 999;
  color: #fff;
}

.nextArrow img {
  -webkit-transform: translate(2px, 0);
          transform: translate(2px, 0);
}

.nextArrow img:focus {
  outline: 0;
}

.backArrow {
  position: absolute;
  top: 50%;
  left: 25px;
  z-index: 999;
  color: #fff;
}

.backArrow img {
  -webkit-transform: translate(-2px, 0);
          transform: translate(-2px, 0);
}

.backArrow img:focus {
  outline: 0;
}

.dots-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  z-index: 50;
  padding: 5px 0;
}

.dot {
  background: #222;
  display: inline-block;
  padding: 10px;
  /* margin-right: 5px; */
  cursor: pointer;
  border-radius: 50%;
}
.active {
  background: #fff;
}

